@tailwind base;
@tailwind components;
@tailwind utilities;
 
@layer base {
  :root {
    /* Base HSL Values */
    --background: 224 71% 4%;    
    --foreground: 213 31% 91%;   
 
    --card: 224 71% 6%;          
    --card-foreground: 213 31% 91%;
 
    --popover: 224 71% 4%;
    --popover-foreground: 213 31% 91%;
 
    --primary: 263 70% 58%;      
    --primary-foreground: 210 40% 98%;
 
    --secondary: 223 47% 11%;    
    --secondary-foreground: 210 40% 98%;
 
    --muted: 223 47% 11%;
    --muted-foreground: 215 20% 65%;
 
    --accent: 180 100% 50%;      
    --accent-foreground: 222 47% 11%;
 
    --destructive: 0 63% 31%;
    --destructive-foreground: 210 40% 98%;
 
    --border: 216 34% 17%;
    --input: 216 34% 17%;
    --ring: 263 70% 58%;
 
    --radius: 0.75rem;
  }
 
  .dark {
    --background: 224 71% 4%;
    --foreground: 213 31% 91%;
  }
}
 
@layer base {
  * {
    @apply border-border;
  }
  
  html, body {
    /* CRITICAL: Chrome Extension Popup Dimensions */
    width: 400px;
    height: 600px;
    overflow: hidden;
    margin: 0;
    padding: 0;
  }

  body {
    @apply bg-background text-foreground;
    font-feature-settings: "rlig" 1, "calt" 1;
    background-image: 
        radial-gradient(circle at 50% 0%, hsl(263 70% 58% / 0.15), transparent 40%),
        radial-gradient(circle at 80% 80%, hsl(180 100% 50% / 0.05), transparent 30%);
  }
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: hsl(var(--muted-foreground) / 0.3);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: hsl(var(--muted-foreground) / 0.5);
}

.animate-pulse-glow {
  animation: pulse-glow 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse-glow {
  0%, 100% {
    opacity: 1;
    box-shadow: 0 0 10px hsl(var(--primary) / 0.5);
  }
  50% {
    opacity: .7;
    box-shadow: 0 0 20px hsl(var(--primary) / 0.8);
  }
}
